import config from 'next/config';
import React from 'react'
import { connect } from 'react-redux'
import { gsap } from "gsap"

import css from './index.scss'

const assetPrefix = config().publicRuntimeConfig.assetPrefix;

class Intro extends React.Component {
  componentDidMount() {
    document.querySelectorAll(`.${css.brief} div:last-child`).forEach(brief => {
      brief.innerHTML = [...brief.innerText].map(char => `<span>${char}</span>`).join('');
    });
    const wheelAnimation = gsap.timeline();
    wheelAnimation.to(`.${css.video}`, {x: '-50%', duration: 1, ease: 'none'});
    wheelAnimation.to(`.${css.video} .${css.view}`, {x: `100%`, duration: 1, ease: 'none'}, '-=1');
    wheelAnimation.to(`.${css.content}`, {x: '0%', duration: 1, ease: 'none'}, '-=1');
    wheelAnimation.to(`.${css.content} .${css.view}`, {x: `50%`, duration: 1, ease: 'none'}, '-=1');
    wheelAnimation.to(`.${css.bubble}`, {y: '25%', x: '25%', duration: 0.75, ease: 'none'}, '-=0.75');
    wheelAnimation.to(`.${css.bubble} .${css.view}`, {bottom: `25%`, left: `-25%`, duration: 0.75, ease: 'none'}, '-=0.75');
    wheelAnimation.pause();
    const introAnimation = gsap.timeline();
    introAnimation.to(`.${css.brief} div`, {top: 0, opacity: 1, stagger: 0.2,duration: 0.6, ease: 'slow(0.7, 0.7, false)'});
    introAnimation.to(`.${css.brief} div:not(:last-child)`, {top: -64, opacity: 0, stagger: 0.2,duration: 0.4, ease: 'slow(0.7, 0.7, false)'}, '+=1');
    introAnimation.set(`.${css.brief} div:last-child`, {display: 'grid', gridAutoFlow: 'column', justifyContent: 'space-around', lineHeight: '1em'}, `-=0.2`);
    introAnimation.to(`.${css.brief}`, {width: '72%', duration: 0.4, ease: 'slow(0.7, 0.7, false)'}, `-=0.2`);
    introAnimation.to(`.${css.brief} div:last-child`, {fontSize: '10vw', duration: 0.2, ease: 'slow(0.7, 0.7, false)'}, '-=0.1');
    introAnimation.to(`.${css.brief}`, {width: '92%', top: '100%', yPercent: -100, duration: 0.4, ease: 'slow(0.7, 0.7, false)'});
    introAnimation.to(`.${css.slide}`, {height: 0, stagger: 0.2, duration: 0.6, ease: 'slow(0.7, 0.7, false)', onStart: () => {
      document.querySelector(`.${css.video} video`).play();

    }}, '-=0.2');
    introAnimation.to(`.${css.intro} > .${css.brief} span`, {opacity: 0, stagger: 0.18, duration: 0, ease: 'slow(0.7, 0.7, false)', onComplete: () => {
      let wheelPointer = 0;
      document.querySelector(`.${css.intro}`).addEventListener('wheel', (event) => {
        if(event.wheelDeltaY < 0 && wheelPointer < 100) {
          wheelPointer = Math.min(100, wheelPointer + (2.4 * (event.wheelDeltaY / -120)));
        }
        if(event.wheelDeltaY > 0 && wheelPointer > 0) {
          wheelPointer = Math.max(0, wheelPointer - (2.4 * (event.wheelDeltaY / 120)));
        }
        wheelAnimation.progress(wheelPointer / 100);
      });
    }}, '-=1.2');
  }
  
  render() {
    return (
      <div className={css.intro}>
        <div className={css.video}>
          <video loop muted preload="auto">
            <source src={`${assetPrefix}/intro.mp4`} type="video/mp4" />
          </video>
          <div className={css.view}>
            <div className={css['brief__title']}>
              {'tronx'.split('').map((char, key) => <span key={key}>{char}</span>)}
            </div>
          </div>
        </div>
        <div className={css.content}>
          <p>
            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Vero, beatae repellendus voluptates reiciendis quae porro, explicabo temporibus recusandae laborum, eaque nobis optio eligendi aperiam asperiores ipsam consequatur provident dignissimos error.
          </p>
          <div className={css.view}>
            <div className={css.grid}>
              <div className={css.cell}>
              </div>
            </div>
            <div className={css['brief__title']}>
              {'tronx'.split('').map((char, key) => <span key={key}>{char}</span>)}
            </div>
          </div>
        </div>
        <div className={css.bubble}>
          <div className={css.circle}>
            <div className={css.view}>
              <div className={css['brief__title']}>
                {'tronx'.split('').map((char, key) => <span key={key}>{char}</span>)}
              </div>
            </div>
          </div>
        </div>
        <div className={css.slides}>
          <div className={css.slide}>
          </div>
          <div className={css.slide}>
          </div>
          <div className={css.slide}>
          </div>
          <div className={css.slide}>
          </div>
        </div>
        <div className={css.brief}>
          <div>this</div>
          <div>is</div>
          <div>tronx</div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  intro_loading: state.intro_loading
});

export default connect(
  mapStateToProps
)(Intro)